import React, { useState } from "react";
import { developerDelete } from "../server/server";

export function DeveloperList(props) {
  const searchOptions = [
    "",
    "",
    "",
    "",
    "react",
    "vue",
    "",
    "",
    "",
  ];

  const { developers, openModal, refreshDeveloperList } = props;
  const [currentDeveloper, setCurrentDeveloper] = useState(null);

  const [searchTerm, setSearchTerm] = useState(""); //  
  const [selectedOptions, setSelectedOptions] = useState([]); //    
  const [includeAll, setIncludeAll] = useState(false); //    
  const [isSearchActive, setIsSearchActive] = useState(false); //   
  const [currentPage, setCurrentPage] = useState(1);
  const itemsPerPage = 10;

  const indexOfLastItem = currentPage * itemsPerPage;
  const indexOfFirstItem = indexOfLastItem - itemsPerPage;

  const handleSearch = () => {
    setIsSearchActive(true); //  
    setCurrentPage(1); //    
  };

  const handleKeyPress = (event) => {
    if (event.key === "Enter") {
      handleSearch();
    }
  };

  const handleCheckboxChange = (option) => {
    setSelectedOptions((prev) =>
      prev.includes(option)
        ? prev.filter((item) => item !== option)
        : [...prev, option]
    );
    updateSearchTerm(option);
  };

  const handleIncludeAllChange = () => {
    setIncludeAll((prev) => !prev);
  };

  const updateSearchTerm = (option) => {
    const terms = searchTerm.split(" ").filter(Boolean);
    if (terms.includes(option)) {
      setSearchTerm(terms.filter((term) => term !== option).join(" "));
    } else {
      setSearchTerm([...terms, option].join(" "));
    }
  };

  const filteredDevelopers = isSearchActive
    ? developers.filter((dev) => {
        const terms = searchTerm.toLowerCase().split(" ").filter(Boolean);

        if (includeAll) {
          return terms.every(
            (term) =>
              (dev.devRank && dev.devRank.toLowerCase().includes(term)) ||
              (dev.devSkillsOne &&
                dev.devSkillsOne.toLowerCase().includes(term)) ||
              (dev.devSkillsTwo &&
                dev.devSkillsTwo.toLowerCase().includes(term))
          );
        } else {
          return terms.some(
            (term) =>
              (dev.devRank && dev.devRank.toLowerCase().includes(term)) ||
              (dev.devSkillsOne &&
                dev.devSkillsOne.toLowerCase().includes(term)) ||
              (dev.devSkillsTwo &&
                dev.devSkillsTwo.toLowerCase().includes(term))
          );
        }
      })
    : developers;

  const currentDevelopers = filteredDevelopers.slice(
    indexOfFirstItem,
    indexOfLastItem
  );

  const totalPages = Math.ceil(filteredDevelopers.length / itemsPerPage);

  const handlePageChange = (pageNumber) => {
    setCurrentPage(pageNumber);
  };

  const renderPageNumbers = () => {
    const pageNumbers = [];
    for (let i = 1; i <= totalPages; i++) {
      pageNumbers.push(
        <button
          key={i}
          onClick={() => handlePageChange(i)}
          className={i === currentPage ? "active" : ""}
        >
          {i}
        </button>
      );
    }
    return pageNumbers;
  };

  const handleReset = () => {
    setSearchTerm("");
    setSelectedOptions([]);
    setIncludeAll(false);
    setIsSearchActive(false); //  
    setCurrentPage(1);
  };

  const handleDetail = (dev) => {
    setCurrentDeveloper(dev);
    openModal(dev);
  };

  const handelInsert = () => {
    setCurrentDeveloper(null);
    openModal(null);
  };

  const handleDelete = async (dev) => {
    if (window.confirm("?")) {
      try {
        await developerDelete(dev); //  API 
        alert(".");
        refreshDeveloperList(); //    
      } catch (error) {
        alert(" : " + error.message);
      }
    }
  };

  return (
    <div>
      <div>
        <h1> </h1>
      </div>
      <div className="devSearch">
        <div>
          <label>
            <input
              type="checkbox"
              checked={includeAll}
              onChange={handleIncludeAllChange}
            />
            
          </label>
          <input
            type="text"
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            onKeyPress={handleKeyPress}
            placeholder=" (  )   "
          />
          <div>
            {searchOptions.map((option) => (
              <label key={option}>
                <input
                  type="checkbox"
                  checked={selectedOptions.includes(option)}
                  onChange={() => handleCheckboxChange(option)}
                />
                {option}
              </label>
            ))}
          </div>

          <button onClick={handleSearch}></button>
          <button onClick={handleReset}></button>
          <button onClick={handelInsert}> </button>
        </div>
        <div>
          <table>
            <thead>
              <tr>
                <td
                  style={{
                    color: "white",
                    backgroundColor: "#5b6671",
                    fontSize: "18px",
                  }}
                >
                   : {filteredDevelopers.length}
                </td>
              </tr>
              <tr>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th> </th>
                <th></th>
                <th></th>
                <th>/</th>
                <th></th>
                <th></th>
              </tr>
            </thead>
            <tbody>
              {currentDevelopers.length > 0 ? (
                currentDevelopers.map((dev) => (
                  <tr key={dev.id}>
                    <td>{dev.devRank}</td>
                    <td>{dev.devCarrer}</td>
                    <td>{dev.devPrice}</td>
                    <td>{dev.devName}</td>
                    <td>{dev.devPhone}</td>
                    <td>{dev.devLoc}</td>
                    <td>{dev.devProjLoc}</td>
                    <td>{dev.devStartTime}</td>
                    <td>{dev.devInterviewTime}</td>
                    <td>{dev.devSiSm}</td>
                    <td>
                      <input
                        type="button"
                        value=""
                        onClick={() => handleDetail(dev)}
                      />
                    </td>
                    <td>
                      <input
                        type="button"
                        value=""
                        onClick={() => handleDelete(dev)}
                      />
                    </td>
                  </tr>
                ))
              ) : (
                <tr>
                  <td colSpan="11">  </td>
                </tr>
              )}
            </tbody>
          </table>
        </div>
        <div className="pagination">{renderPageNumbers()}</div>
      </div>
    </div>
  );
}
