export const developerSearch = async () => {
  try {
    const response = await fetch(
      "http://www.giantsolution.co.kr/server/devSearch"
    );
    if (!response.ok) {
      throw new Error("   .");
    }
    const data = await response.json();
    return data;
  } catch (error) {
    alert("", error);
    throw error;
  }
};

export const developerInsert = async (developer) => {
  try {
    const response = await fetch(
      "http://125.133.48.26:8030/projectDataInsert",
      {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(developer),
      }
    );

    if (!response.ok) {
      throw new Error(" .");
    }

    const data = await response.json();
    return data;
  } catch (error) {
    alert(": " + error.message);
    throw error;
  }
};

